import {
  ChangeEvent,
  Dispatch,
  SetStateAction,
  useEffect,
  useState,
} from "react";
import {
  Badge,
  Box,
  Button,
  Card,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormControl,
  IconButton,
  TextField,
  Typography,
} from "@mui/material";
import { Edit as EditIcon } from "@mui/icons-material";
// eslint-disable-next-line import/no-extraneous-dependencies
import { match } from "ts-pattern";
import { useTheme } from "@mui/material/styles";
import { BasicSelect } from "./SchemaPoint";

const displayTypes = ["Indicator", "Float", "PointEnum", "Widget"];

interface PointContentProps {
  topic: string;
  displayType: string | null;
  float?: string;
  unit?: string;
}
function PointContent({
  topic,
  displayType,
  float,
  unit,
}: PointContentProps): JSX.Element {
  const theme = useTheme();
  return (
    <Card
      style={{
        width: 300,
        display: "flex",
        padding: 15,
        alignItems: "center",
        justifyContent: "space-between",
      }}
    >
      <Typography style={{ fontFamily: "monospace" }}>{topic}</Typography>
      <Box style={{ display: "flex" }}>
        {match(displayType)
          .with("Float", () => <Typography variant="body1">{float}</Typography>)
          .with("Indicator", () => (
            <Box
              style={{
                background:
                  Math.random() > 0.5
                    ? theme.palette.success.main
                    : theme.palette.error.main,
                height: 16,
                width: 16,
                borderRadius: "50%",
              }}
            />
          ))
          .otherwise(() => `Type ${displayType} not supported`)}
        <Typography ml={1}>{unit}</Typography>
      </Box>
    </Card>
  );
}
interface PointDialogProps {
  dialog: boolean;
  setDialog: Dispatch<SetStateAction<boolean>>;
  setTopic: Dispatch<SetStateAction<string>>;
  setUnit: Dispatch<SetStateAction<string>>;
  setDisplayType: Dispatch<SetStateAction<typeof displayTypes[number]>>;
}

function PointDialog(p: PointDialogProps): JSX.Element {
  const [topic, setTopic] = useState("");
  const [unit, setUnit] = useState("");
  const [displayType, setDisplayType] = useState("");
  const onClose = (): void => {
    p.setDialog(false);
  };

  const handleSave = (): void => {
    p.setTopic(topic);
    p.setUnit(unit);
    if (displayType) {
      p.setDisplayType(displayType);
    }
    p.setDialog(false);
  };
  const onTopicChange = (event: ChangeEvent<HTMLInputElement>): void => {
    setTopic(event.target.value as typeof displayTypes[number]);
  };
  const onUnitChange = (event: ChangeEvent<HTMLInputElement>): void => {
    setUnit(event.target.value as typeof displayTypes[number]);
  };
  return (
    <Dialog onClose={onClose} open={p.dialog} fullWidth>
      <DialogTitle>Configure Point</DialogTitle>
      <DialogContent
        style={{
          padding: 30,
        }}
      >
        <Box mb={2}>
          <TextField
            value={topic}
            onChange={onTopicChange}
            autoFocus
            margin="dense"
            label="MQTT Topic"
            fullWidth
          />
        </Box>
        <FormControl style={{ display: "flex" }} required>
          <Box style={{ display: "flex", alignItems: "center" }}>
            <Box mr={2} style={{ width: 200 }}>
              <BasicSelect
                setValue={setDisplayType}
                options={displayTypes}
                label="Display Types"
              />
            </Box>
            <TextField value={unit} onChange={onUnitChange} label="Unit" />
          </Box>
        </FormControl>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}>Cancel</Button>
        <Button onClick={handleSave}>Save</Button>
      </DialogActions>
    </Dialog>
  );
}
export default function SchemalessPoint(): JSX.Element {
  const [topic, setTopic] = useState("met/1/temperature");
  const [unit, setUnit] = useState("Â°C");
  const [displayType, setDisplayType] =
    useState<typeof displayTypes[number]>("Float");

  const [editIcon, setEditIcon] = useState(false);
  const [dialog, setDialog] = useState(false);

  const [float, setFloat] = useState("0.00");
  useEffect(() => {
    const interval = setInterval(() => {
      setFloat((Math.random() * 101).toFixed(2));
    }, 1000);
    return () => clearInterval(interval);
  }, []);
  return (
    <Box
      onMouseEnter={(): void => setEditIcon(true)}
      onMouseLeave={(): void => setEditIcon(false)}
    >
      {editIcon ? (
        <Badge
          badgeContent={
            <IconButton onClick={(): void => setDialog(true)}>
              <EditIcon style={{ color: "white" }} fontSize="small" />
            </IconButton>
          }
          color="primary"
        >
          <PointContent
            unit={unit}
            float={float}
            topic={topic}
            displayType={displayType}
          />
        </Badge>
      ) : (
        <PointContent
          unit={unit}
          float={float}
          topic={topic}
          displayType={displayType}
        />
      )}
      <PointDialog
        setDisplayType={setDisplayType}
        setTopic={setTopic}
        dialog={dialog}
        setUnit={setUnit}
        setDialog={setDialog}
      />
    </Box>
  );
}
